// TIPOS
type UserWithTerreiro = Doc<"users"> & { terreiro?: Doc<"terreiros"> | null };

export default function AdminPage() {
  const { isLoading, isAuthenticated } = useConvexAuth();

  if (isLoading) {
    return <div className="p-8">Carregando autenticação...</div>;
  }
  if (!isAuthenticated) {
    return <div className="p-8">Acesso negado. Por favor, faça login.</div>;
  }
  return <AdminDashboard />;
}

function AdminDashboard() {
  const currentUser = useQuery(api.admin.getCurrentUserWithAdminRole);

  if (currentUser === undefined) {
    return <div className="p-8">Verificando permissões...</div>;
  }
  if (currentUser === null || !currentUser.isAdmin) {
    return <div className="p-8">Você não tem permissão para acessar esta página.</div>;
  }
  if (!currentUser.terreiroId) {
    return <div className="p-8">Você precisa estar associado a um terreiro para administrar.</div>;
  }

  return (
    <div className="container mx-auto p-4 md:p-8 space-y-12">
      <div>
        <h1 className="text-3xl font-bold">Painel de Administração</h1>
        <p className="text-muted-foreground">
          Gerencie os membros, eventos e diagnósticos do seu terreiro.
        </p>
      </div>

      <UserManagementPanel terreiroId={currentUser.terreiroId} />
      <DiagnoseMemberPanel terreiroId={currentUser.terreiroId} />
      <EventManagementPanel terreiroId={currentUser.terreiroId} />
      {/* NOVOS PAINÉIS */}
      <BathManagementPanel terreiroId={currentUser.terreiroId} />
      <RitualManagementPanel terreiroId={currentUser.terreiroId} />
    </div>
  );
}

// PAINEL DE USUÁRIOS (Sem alterações)
function UserManagementPanel({ terreiroId }: { terreiroId: Id<"terreiros"> }) {
  const users = useQuery(api.admin.getUsers, { terreiroId }) || [];
  const terreiros = useQuery(api.admin.getTerreiros) || [];
  const updateUserRole = useMutation(api.admin.updateUserRole);
  const updateUserTerreiro = useMutation(api.admin.updateUserTerreiro);

  return (
    <section>
      <h2 className="text-2xl font-semibold mb-4 border-b pb-2">Gerenciar Membros</h2>
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>Nome</TableHead>
            <TableHead>Email</TableHead>
            <TableHead>Papel</TableHead>
            <TableHead>Terreiro</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {users.map((user: UserWithTerreiro) => (
            <TableRow key={user._id}>
              <TableCell>{user.name}</TableCell>
              <TableCell>{user.email}</TableCell>
              <TableCell>
                <Select
                  defaultValue={user.role}
                  onValueChange={(role) => updateUserRole({ userId: user._id, role: role as "admin" | "member" })}
                >
                  <SelectTrigger><SelectValue /></SelectTrigger>
                  <SelectContent>
                    <SelectItem value="member">Membro</SelectItem>
                    <SelectItem value="admin">Admin</SelectItem>
                  </SelectContent>
                </Select>
              </TableCell>
              <TableCell>
                <Select
                  defaultValue={user.terreiroId}
                  onValueChange={(id) => updateUserTerreiro({ userId: user._id, terreiroId: id as Id<"terreiros"> })}
                >
                  <SelectTrigger><SelectValue placeholder="Associar..." /></SelectTrigger>
                  <SelectContent>
                    {terreiros.map((t) => <SelectItem key={t._id} value={t._id}>{t.name}</SelectItem>)}
                  </SelectContent>
                </Select>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </section>
  );
}

// PAINEL DE DIAGNÓSTICOS (Atualizado)
function DiagnoseMemberPanel({ terreiroId }: { terreiroId: Id<"terreiros"> }) {
  const members = useQuery(api.admin.getUsers, { terreiroId })?.filter(u => u.role === 'member') || [];

  return (
    <section>
      <h2 className="text-2xl font-semibold mb-4 border-b pb-2">Diagnosticar Membros</h2>
      <Table>
        <TableHeader><TableRow><TableHead>Nome</TableHead><TableHead>Ações</TableHead></TableRow></TableHeader>
        <TableBody>
          {members.map((member) => (
            <TableRow key={member._id}>
              <TableCell>{member.name}</TableCell>
              <TableCell><DiagnoseMemberModal member={member} terreiroId={terreiroId} /></TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </section>
  );
}

// PAINEL DE EVENTOS (Sem alterações)
function EventManagementPanel({ terreiroId }: { terreiroId: Id<"terreiros"> }) {
  const events = useQuery(api.events.get, { terreiroId }) || [];
  const deleteEvent = useMutation(api.events.deleteEvent);

  return (
    <section>
      <h2 className="text-2xl font-semibold mb-4 border-b pb-2 flex justify-between items-center">
        <span>Gerenciar Eventos</span>
        <CreateEventModal terreiroId={terreiroId} />
      </h2>
      <Table>
        <TableHeader><TableRow><TableHead>Título</TableHead><TableHead>Data</TableHead><TableHead>Público</TableHead><TableHead>Ações</TableHead></TableRow></TableHeader>
        <TableBody>
          {events.map((event) => (
            <TableRow key={event._id}>
              <TableCell>{event.title}</TableCell>
              <TableCell>{format(new Date(event.date), "dd/MM/yyyy")}</TableCell>
              <TableCell>{event.isPublic ? "Sim" : "Não"}</TableCell>
              <TableCell><Button variant="destructive" size="sm" onClick={() => deleteEvent({ eventId: event._id })}><Trash2 className="h-4 w-4" /></Button></TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </section>
  );
}

