// --- MODAIS ---

// MODAL DE DIAGNÓSTICO (Atualizado)
function DiagnoseMemberModal({ member, terreiroId }: { member: Doc<"users">; terreiroId: Id<"terreiros"> }) {
    const [isOpen, setIsOpen] = useState(false);
    const [diagnosisType, setDiagnosisType] = useState<"event" | "bath" | "ritual">("event");

    const events = useQuery(api.events.get, { terreiroId }) || [];
    const baths = useQuery(api.baths.get, { terreiroId }) || [];
    const rituals = useQuery(api.rituals.get, { terreiroId }) || [];

    const createDiagnosis = useMutation(api.diagnoses.createDiagnosis);
    const { register, handleSubmit, control, watch } = useForm();
    const selectedReferenceId = watch("referenceId");

    const onSubmit = (data: any) => {
        createDiagnosis({
            memberId: member._id,
            type: diagnosisType,
            referenceId: data.referenceId,
            notes: data.notes,
        });
        setIsOpen(false);
    };

    const getReferenceItems = () => {
        switch (diagnosisType) {
            case "event": return events;
            case "bath": return baths;
            case "ritual": return rituals;
            default: return [];
        }
    };

    return (
        <Dialog open={isOpen} onOpenChange={setIsOpen}>
            <DialogTrigger asChild><Button size="sm">Diagnosticar</Button></DialogTrigger>
            <DialogContent>
                <DialogHeader><DialogTitle>Diagnosticar {member.name}</DialogTitle></DialogHeader>
                <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
                    <div className="space-y-2">
                        <Label>Tipo de Diagnóstico</Label>
                        <Select onValueChange={(value) => setDiagnosisType(value as any)} defaultValue={diagnosisType}>
                            <SelectTrigger><SelectValue /></SelectTrigger>
                            <SelectContent>
                                <SelectItem value="event">Evento</SelectItem>
                                <SelectItem value="bath">Banho</SelectItem>
                                <SelectItem value="ritual">Ritual</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>
                    <div className="space-y-2">
                        <Label>Item Específico</Label>
                        <Controller
                            name="referenceId"
                            control={control}
                            rules={{ required: true }}
                            render={({ field }) => (
                                <Select onValueChange={field.onChange} value={field.value}>
                                    <SelectTrigger><SelectValue placeholder="Selecione um item..." /></SelectTrigger>
                                    <SelectContent>
                                        {getReferenceItems().map((item) => (
                                            <SelectItem key={item._id} value={item._id}>{item.title}</SelectItem>
                                        ))}
                                    </SelectContent>
                                </Select>
                            )}
                        />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="notes">Observações</Label>
                        <Textarea id="notes" {...register("notes")} />
                    </div>
                    <DialogFooter>
                        <Button type="submit" disabled={!selectedReferenceId}>Salvar Diagnóstico</Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}

// MODAL DE CRIAR EVENTO (Sem alterações)
function CreateEventModal({ terreiroId }: { terreiroId: Id<"terreiros"> }) {
    const [isOpen, setIsOpen] = useState(false);
    const createEvent = useMutation(api.events.create);
    const { register, handleSubmit, control, reset } = useForm();

    const onSubmit = (data: any) => {
        createEvent({
            ...data,
            date: new Date(data.date).getTime(),
            terreiroId,
        });
        reset();
        setIsOpen(false);
    };

    return (
        <Dialog open={isOpen} onOpenChange={setIsOpen}>
            <DialogTrigger asChild><Button size="sm"><PlusCircle className="h-4 w-4 mr-2" />Novo Evento</Button></DialogTrigger>
            <DialogContent>
                <DialogHeader><DialogTitle>Criar Novo Evento</DialogTitle></DialogHeader>
                <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
                    <div className="space-y-2"><Label htmlFor="title">Título</Label><Input id="title" {...register("title", { required: true })} /></div>
                    <div className="space-y-2"><Label htmlFor="description">Descrição</Label><Textarea id="description" {...register("description")} /></div>
                    <div className="space-y-2"><Label>Data</Label>
                        <Controller name="date" control={control} rules={{ required: true }} render={({ field }) => (
                            <Popover><PopoverTrigger asChild>
                                <Button variant={"outline"} className={cn("w-full justify-start text-left font-normal", !field.value && "text-muted-foreground")}>
                                    <CalendarIcon className="mr-2 h-4 w-4" />
                                    {field.value ? format(field.value, "PPP", { locale: ptBR }) : <span>Escolha uma data</span>}
                                </Button>
                            </PopoverTrigger><PopoverContent className="w-auto p-0"><Calendar mode="single" selected={field.value} onSelect={field.onChange} initialFocus /></PopoverContent></Popover>
                        )} />
                    </div>
                    <div className="flex items-center space-x-2"><Controller name="isPublic" control={control} render={({ field }) => (<Checkbox id="isPublic" checked={field.value} onCheckedChange={field.onChange} />)} /><label htmlFor="isPublic">Evento Público?</label></div>
                    <DialogFooter><Button type="submit">Criar Evento</Button></DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}

// NOVO: MODAL DE CRIAR BANHO
function CreateBathModal({ terreiroId }: { terreiroId: Id<"terreiros"> }) {
    const [isOpen, setIsOpen] = useState(false);
    const createBath = useMutation(api.baths.create);
    const { register, handleSubmit, reset } = useForm();

    const onSubmit = (data: any) => {
        createBath({ ...data, terreiroId });
        reset();
        setIsOpen(false);
    };

    return (
        <Dialog open={isOpen} onOpenChange={setIsOpen}>
            <DialogTrigger asChild><Button size="sm"><PlusCircle className="h-4 w-4 mr-2" />Nova Receita</Button></DialogTrigger>
            <DialogContent>
                <DialogHeader><DialogTitle>Criar Nova Receita de Banho</DialogTitle></DialogHeader>
                <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
                    <div className="space-y-2"><Label htmlFor="title">Título</Label><Input id="title" {...register("title", { required: true })} /></div>
                    <div className="space-y-2"><Label htmlFor="description">Descrição (Modo de preparo)</Label><Textarea id="description" {...register("description")} /></div>
                    <div className="space-y-2"><Label htmlFor="ingredients">Ingredientes</Label><Textarea id="ingredients" {...register("ingredients", { required: true })} /></div>
                    <DialogFooter><Button type="submit">Criar Receita</Button></DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}

